import shareView from '@/components/share/share';
export default {
	data() {
		return {
			detail: '',
			isShow: 0
		}
	},
	components:{shareView},
	onLoad(e) {
		this.getData(e.id);
		console.log(e.id)
	},
	methods: {
		getData (id) {
			var t = this;
			uni.showLoading({
				title: "加载中"
			});
			uni.request({
				url: t.$serverUrl + '/detail',
				data: { 
					appkey: t.$appkey,
					id: id
				},
				success: (ret) => {
					const data = ret.data.result;
					t.detail = data;
					uni.hideLoading();
					t.isShow = !t.isShow;
				}
			});
			
		},
	},
	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			return {
				title: this.detail.name,
				desc: this.detail.content,
				imageUrl: this.detail.pic,
				success: res => {},
				fail: err => {}
			}
		}
		return {
			title: this.detail.name,
			desc: this.detail.content,
			imageUrl: this.detail.pic,
			success: res => {},
			fail: err => {}
		}
	}
}
